@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navBarContainer{background-color:#FFE600;height:64px;display:flex;justify-content:space-between;align-items:center;position:relative}.navBarContainer__logo{width:150px}.navBarContainer__compraAhora{width:350px;position:absolute;right:30px}.navBarContainer__searchContainer{position:absolute;width:45%;left:50%;transform:translate(-50%, 0px)}.navBarContainer__input{height:35px;width:100%;border:none;border-radius:5px;outline:none;padding-left:10px;font-size:20px;font-weight:400}.navBarContainer__searchButton{background-color:transparent;border:none;position:absolute;right:0;top:1px;border-left:1px solid #dedddd;height:90%;cursor:pointer}@media (max-width: 720px){.navBarContainer__logo{display:none}.navBarContainer__searchContainer{width:80%}}

.productItem{background-color:white;padding:10px;border-radius:5px;cursor:pointer;transition:height .3s;width:250px;height:340px;font-family:'Nunito', sans-serif}.productItem:hover{height:380px}.productItem:hover>.productItem__moreInfo{height:20px;opacity:1}.productItem__separation{position:relative}.productItem__line{border:1px solid #dedddd;width:94%}.productItem__container-carrito{border:1px solid #dedddd;border-radius:50%;padding:5px;display:flex;justify-content:center;width:20px;align-items:center;position:absolute;background-color:#fff;top:-13px;right:15px}.productItem__carrito{width:20px;fill:#39b54a}.productItem__img{width:200px;height:250px;display:flex;margin:0 auto}.productItem__price{text-align:left;color:#333333;font-size:23px;font-weight:600;margin:5px auto;width:90%}.productItem__cuota{margin:5px auto;color:#333333;width:90%;font-size:14px;font-weight:600}.productItem__moreInfo{height:0px;transition:opacity .2s .2s, height .2s .2s;opacity:0}.productItem_info{width:94%;margin:0 auto;font-weight:500;color:#505050;font-size:14px}

.homeContainer{background-color:#EBEBEB}.homeContainer__products{margin:10px 50px;display:grid;grid-gap:20px;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-auto-rows:400px}

.pais__item{background-color:#ffffff82;margin:1px 0px;padding:1px 11px;border-radius:5px}.pais__item:hover{background-color:#ffffffad}

.containerPaises{background-color:#FFE600;min-height:100vh;display:flex;justify-content:center;align-items:center}.containerPaises__logo{width:400px}.containerPaises__listaPaises{display:flex;flex-direction:column}

.product__details{display:flex;max-width:1120px;margin:0 auto;justify-content:space-between;padding-top:50px}

.productImgViewer{display:grid;grid-template-columns:150px 400px;height:400px}.productImgViewer__img{width:400px;height:400px}.miniImgContainer{display:grid;grid-template-columns:auto;grid-auto-rows:76px}

.minProductImg__item{width:60px;height:60px;margin-top:10px;border-radius:5px;cursor:pointer;border:1px solid #c7c7c7}.minProductImg__item:hover{border:2px solid #486de6}

.productInfo{width:350px;border:1px solid #cccccc;border-radius:5px;display:flex;flex-direction:column;padding:10px 30px;box-sizing:border-box;grid-gap:10px;gap:10px;padding-bottom:10px;margin-bottom:10px}.productInfo__nuevo{color:#969696;font-size:14px;font-weight:400;vertical-align:3px;white-space:pre-wrap}.productInfo__title{color:rgba(0,0,0,0.8);font-size:22px;font-weight:600;padding-bottom:8px;line-height:1.18;padding-right:10px;word-break:break-word}.productInfo__precio-anterior{font-size:20px;color:rgba(0,0,0,0.45);text-decoration:line-through}.productInfo__price{font-size:36px;color:#000;font-weight:300}.productInfo__envio-normalidad{font-size:14px;padding:6px 8px;text-transform:none;background-color:rgba(65,137,230,0.1);color:#3483fa;font-weight:600;line-height:1;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.productInfo__envio-gratis{color:#00a650;font-size:16px;margin-bottom:2px;line-height:1.3;display:flex}.productInfo__camion{margin:0;fill:#00a650;margin-right:10px}.productInfo__full{fill:#00a650;position:relative;top:5px;height:16px;width:56px;margin-top:-1px}.productInfo__stock-disponible{font-size:16px;color:rgba(0,0,0,0.8);font-weight:600}.productInfo__button{font-size:16px;font-weight:600;line-height:48px;padding:0 24px;height:48px;text-align:center;border-radius:6px;transition:.18s ease-out;display:inline-block;width:auto;-webkit-font-smoothing:antialiased;cursor:pointer;border:none;outline:none}.productInfo--blue{background-color:#3483fa;color:#fff}.productInfo--blue:hover{background-color:#005ae0}.productInfo--lightblue{background-color:rgba(65,137,230,0.15);color:#3483fa}.productInfo--lightblue:hover{background-color:rgba(2,30,66,0.15)}.productInfo__caracteristicas{color:grey;font-size:15px}

